/* hidden */
.sr-only,
.hidden,
.accessibility {
position: absolute;
width: 1px;
height: 1px;
margin: -1px;
padding: 0;
overflow: hidden;
clip: rect(0, 0, 0, 0);
border: 0;
}

/* clear float */
.clear {
clear: both;
}

.clearfix {
display: inline-block;
}

.clearfix {
display: block;
}

.clearfix:before, .clearfix:after {
content: ".";
display: block;
height: 0;
clear: both;
visibility: hidden;
}

/* skip */
.skip {
position: absolute;
top: 0;
left: 0;
z-index: 9999;
}

.skip a {
position: absolute;
display: block;
width: 500px;
left: 0;
text-align: center;
top: -10000px;
z-index: 5000;
font-size: 1.3em;
}

.skip a:hover,
.skip a:focus,
.skip a:active {
position: absolute;
top: 0;
padding: 15px 0;
font-weight: bold;
color: #fff;
background: #1d60a7;
line-height: 1;
}

